
.container{
  background: linear-gradient(to right bottom,#12343b, #c89666);
  width :100%;
  min-height:100vh;
  display:flex;
  justify-content:center;
  align-items:center;
}

.app-wrapper{
  background-color: #12343b;
  min-width: 450px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}


  .header h1{
    color: rgb(243, 255, 238);
    text-align: center;
    margin: 30px 0;
  }


.task-input{
  background-color: #000000;
  border: 1px solid #c89666;
  border-radius: 10px;
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  justify-content: center;
  font-size: 20px;
  color: #ccc;
}

.button-add{
  width: 70px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #f1af71;
  cursor: pointer;
}

.list-item{
  display: flex;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-complete,
.button-delete,
.button-edit{
  border:none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete{
  color: #ff6c6c;
  margin-right: 10px;
}

.button-edit{
 color: #e2d029;
 margin-right: 10px;
}

.button-delete{
  color: lightseagreen;

}

.complete{
  text-decoration-color: #ff6c6c;
  text-decoration-line: line-through;
  text-decoration-style:solid ;
  opacity: 0.6;
}

.list{
width: 220px;
background-color: #12343b;
border: none;
color: #ccc;
font-size: 20px;
padding-left: 10px;
margin-right: 15px;
}


input:focus,
button:focus{
  outline: none;
}








